<template>
    <div class="backimg">
  <div style="font-size:50px;color: white;">
      대출일 {{nowDate}}
    </div>
    <br/>
    <div style="font-size:50px;color: white;">
      반납일 {{retDate}}
    </div>
</div>

</template>

<script>
export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  data() {
    return {
      timer: null,
      nowWeek: '',
      nowDate: '',
      nowTime: ''
    }
  },
  mounted() {
    this.timer = setInterval(() => {
      this.setNowTimes()
    }, )
  },
  methods: {
    setNowTimes() {
      //      
      let myDate = new Date()
      let mm = myDate.getMonth() + 1
      let dd = String(myDate.getDate() < 10 ? '0' + myDate.getDate() : myDate.getDate())
      this.nowDate = mm + '월' + dd+'일'
      let ret=new Date(myDate.setDate(myDate.getDate()+14))
      let rm = ret.getMonth() + 1
      let rd = String(ret.getDate() < 10 ? '0' + ret.getDate() : ret.getDate())
      this.retDate =  rm + '월' + rd+'일'


    }
  }
}
</script>

